<template>
    <div>
        <div class="pad-h-2"></div>
        <div class="container grid">
            <div class="col-2 pad-right-2">
                <div class="pannel-widget">
                    <h2 class="base-title-widget">个人中心</h2>
                    <hr class="seg-widget"/>

                    <!--侧边栏导航组件-->
                    <aside-nav-widget :init_data="aside_nav_data"></aside-nav-widget>

                </div>
            </div>
            <div class="col-10">
                <div class="pannel-widget">
                    <h3 class="base-title-widget bt-w-blue">成绩单</h3>
                    <hr class="seg-widget"/>
                    <div class="pad-h-1"></div>

                    <!--基本列表文本-->
                    <div class="list-text-widget grid pad-w-2">
                        <div class="col-6">
                            <div class="lt-w-item">
                                <span>班级排名：</span>
                                <span class="lt-w-item-after">{{report.class_rank}}</span>
                            </div>
                        </div>
                        <div class="col-6">
                            <div class="lt-w-item">
                                <span>月排名：</span>
                                <span class="lt-w-item-after">{{report.month_rank}}</span>
                            </div>
                        </div>
                        <div class="col-6">
                            <div class="lt-w-item">
                                <span>总排名：</span>
                                <span class="lt-w-item-after">{{report.total_rank}}</span>
                            </div>
                        </div>
                        <div class="col-6">
                            <div class="lt-w-item">
                                <span>昨日进度：</span>
                                <span class="lt-w-item-after">{{report.progress}}</span>
                            </div>
                        </div>
                        <div class="col-6">
                            <div class="lt-w-item">
                                <span>预计完成时间：</span>
                                <span class="lt-w-item-after">{{report.end_time}}</span>
                            </div>
                        </div>
                    </div>

                </div>
                <div class="pad-h-1"></div>
                <div class="pannel-widget">
                    <h3 class="base-title-widget bt-w-blue">任务线</h3>
                    <hr class="seg-widget"/>

                    <!--任务线-->
                    <div class="task-line-widget">
                        <ul class="tl-w-list">
                            <li class="tl-w-item" :state="task.act" v-for="task of task_line">
                                <span class="tl-w-date">{{task.date}}</span>
                                <span>{{task.task}}</span>
                            </li>
                        </ul>
                    </div>

                    <div class="pad-left-2"><a @click="show_more_task">查看更多</a></div>
                    <div class="pad-h-1"></div>
                </div>
                <div class="pad-h-2"></div>
            </div>
        </div>
    </div>
</template>

<script>
    import AsideNav from "@/components/widget/aside-nav-widget";

    var report = {//【data:成绩单】
            class_rank: "第 1 名",
            month_rank: "第 15 名",
            total_rank: "第 3 名（2018-10-10）",
            progress: "JAVA反射",
            end_time: "2018-10-14"
        }, task_line = [//【data:任务线】
            {
                date: "10月9日",
                task: "完成 Hadoop 基础入门作业提交"
            },
            {
                date: "10月9日",
                task: "完成 Java 课程学习"
            },
            {
                date: "10月8日",
                task: "完成 Hbase 阶段学习",
                act: "act",
            },
            {
                date: "10月7日",
                task: "完成 HTML 课程学习"
            },
            {
                date: "10月6日",
                task: "完成 CSS 课程学习"
            },
        ],
        user_blog = "http://www.baidu.com",//【data:用户博客地址】
        aside_nav_data = [
            {
                name: "我的成绩",
                act: "act",
                type: "route",
                link: "/user_home",
            },
            {
                name: "我的作业",
                type: "route",
                link: "/user_home_work",
            },
            {
                name: "我的博客",
                type: "href",
                link: user_blog,
            },
            {
                name: "我的简历",
                type: "route",
                link: "/user_home_resume",
            },
            {
                name: "设置",
            },
        ];

    export default {
        name: "user_home",
        data() {
            return {

                /*aside_nav_widget*/
                aside_nav_data,

                /*成绩单*/
                report,

                /*任务线*/
                task_line,

            }
        },
        components: {
            "aside-nav-widget": AsideNav,
        },
        methods: {

            /*查看更多任务？？？？*/
            show_more_task() {
                console.log(this.task_line)
            }

        }
    }
</script>

<style scoped>
    @import "../assets/css/pannel-widget.css";
    @import "../assets/css/seg-widget.css";
    @import "../assets/css/base-title-widget.css";
    @import "../assets/css/list-text-widget.css";
    @import "../assets/css/task-line-widget.css";
</style>


