<template>
    <div class="container">
        <h2 class="main-title-widget">{{cur_class.name}}</h2>
        <class-path-widget :cur_class="cur_class"></class-path-widget>
    </div>
</template>

<script>
    import ClassPath from '@/components/widget/class-path-widget';

    var cur_class = {//【data:当前课程】
        name: "Java",
        sub_class: [
            {
                sub_c_id: "a_1",
                name: "Java初识",
                desc: "对java进行简单介绍，Java程序如何执行，以及Java程序的结构。"
            },
            {
                sub_c_id: "a_2",
                name: "Java常量与变量",
                desc: "java的基本概念，包括标识符，关键字，数据类型，字面量，变量和常量。"
            },
            {
                sub_c_id: "a_3",
                name: "Java运算符",
                desc: "算术、赋值、逻辑和条件等常用运算符，以及简单的条件语句和 if-else。"
            },
            {
                sub_c_id: "a_4",
                name: "Java流程控制之选择结构",
                desc: "进一步学习选择结构，包括多重 if 与嵌套结构，以及 switch 语句。"
            },
            {
                sub_c_id: "a_5",
                name: "Java流程控制之循环结构",
                desc: "掌握 while 循环、do-while 循环、for 循环和循环嵌套的使用。"
            },
            {
                sub_c_id: "a_6",
                name: "Java数组之一维数组",
                desc: "介绍一维数组的定义和使用，以及增强型 for 循环的应用，并能够实现冒泡排序。"
            },
        ]
    }

    export default {
        name: "learn_path",
        components: {
            'class-path-widget': ClassPath,
        },
        data(){
            return {

                /*当前课程*/
                cur_class: cur_class,

            }
        }
    }
</script>

<style scoped>
    @import "../assets/css/main-title-widget.css";
</style>


