<template>
    <div>
        <div class="pad-h-2"></div>
        <div class="container grid">
            <div class="col-2 pad-right-2">
                <div class="pannel-widget">
                    <h2 class="base-title-widget">竞技场</h2>
                    <hr class="seg-widget"/>

                    <!--侧边栏导航组件-->
                    <ul class="aside-nav-widget">
                        <li class="an-w-btn act" data-type="TEXT">排行榜</li>
                        <li class="an-w-btn" data-type="IMG">闯关模式</li>
                        <li class="an-w-btn" data-type="VIDEO">对战模式</li>
                        <li class="an-w-btn" data-type="XMIND">团赛模式</li>
                    </ul>

                </div>
            </div>
            <div class="col-10">
                <div class="pannel-widget">
                    <ul class="p-w-tabs" @click="sel_rank">
                        <li type="total_rank" class="p-w-tab act">总排行</li>
                        <li type="week_rank" class="p-w-tab">周排行</li>
                        <li type="day_rank" class="p-w-tab">日排行</li>
                    </ul>

                 <!--排行榜组件-->
                    <div class="base-table-widget">
                        <table class="bt-w-table">
                            <thead>
                            <tr class="bt-w-title">
                                <td class="bt-w-title-td">学员</td>
                                <td class="bt-w-title-td pcenter">分数</td>
                                <td class="bt-w-title-td pcenter">排名</td>
                            </tr>
                            </thead>
                            <tbody>
                            <tr class="bt-w-tr" v-for="(user, index) of cur_rank">
                                <td class="bt-w-td"><img class="bt-w-cover" src="@/assets/user/u1/cover.jpg" />{{user.name}}</td>
                                <td class="bt-w-td pcenter">{{user.score}}</td>
                                <td class="bt-w-td pcenter"><i class="icon-wuxing star-ico-widget" :rank="4-index" v-if="index<3"></i> <span class="bt-w-score">{{index+1}}</span></td>
                            </tr>
                            </tbody>
                        </table>
                    </div>

                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import $B from '@/lib/Btx';

    var rank_data = {//【data:排行榜数据】
        total_rank: [
            {
                uid: "u1",
                name: "张雨生",
                score: "340",
            },
            {
                uid: "u2",
                name: "周洁",
                score: "120",
            },
            {
                uid: "u3",
                name: "王林",
                score: "113",
            },
            {
                uid: "u4",
                name: "刘建明",
                score: "107",
            },
            {
                uid: "u5",
                name: "吴建豪",
                score: "101",
            },
            {
                uid: "u6",
                name: "周子轩",
                score: "98",
            },
            {
                uid: "u7",
                name: "杨洋",
                score: "85",
            },
        ],
        week_rank: [
            {
                uid: "u6",
                name: "周子轩",
                score: "98",
            },
            {
                uid: "u7",
                name: "杨洋",
                score: "85",
            },
        ],
        day_rank: [
            {
                uid: "u4",
                name: "刘建明",
                score: "107",
            },
            {
                uid: "u5",
                name: "吴建豪",
                score: "101",
            },
        ]
    }

    export default {
        name: "arena",
        data() {
            return {

                /*全部排行榜*/
                rank_data: rank_data,

                /*当前排行榜*/
                cur_rank: rank_data.total_rank,
            }

        },
        methods: {

            /*排行选择*/
            sel_rank(e){
                $B.select("p-w-tab", e.target, ()=>{
                    this.cur_rank = this.rank_data[e.target.type];
                });
            }

        }
    }
</script>

<style scoped>
    @import "../assets/css/pannel-widget.css";
    @import "../assets/css/seg-widget.css";
    @import "../assets/css/base-title-widget.css";
    @import "../assets/css/aside-nav-widget.css";
    @import "../assets/css/base-table-widget.css";
    @import "../assets/css/star-ico-widget.css";
</style>


