                            <div class="search-widget">
                                <div class="s-w-inp">
                                    <i class="s-w-inp-ico icon-sousuo"></i>
                                    <input v-model="search_work_name" class="s-w-inp-text" type="text" placeholder="搜索您的文件 ..."/>
