            },
            {
                name: "我的简历",
                type: "route",
                link: "/user_home_resume",
            },
            {
                name: "设置",
            },
        ];

    export default {
        name: "user_home_work",
        components: {
            "aside-nav-widget": AsideNav,
        },
        data() {
            return {

                /*aside_nav_widget*/
                aside_nav_data,

                /*我的作业*/
                work_list,

                /*搜索作业名*/
                search_work_name: "",

