<template>
    <div>
        <div class="pad-h-2"></div>
        <div class="container grid">
            <div class="col-2 pad-right-2">
                <div class="pannel-widget">
                    <h2 class="base-title-widget">图书馆</h2>
                    <hr class="seg-widget"/>

                    <!--侧边栏导航组件-->
                    <ul class="aside-nav-widget" @click="sel_search_type">
                        <li class="an-w-btn act" data-type="ALL">知识库</li>
                        <li class="an-w-btn" data-type="TEXT">文字</li>
                        <li class="an-w-btn" data-type="IMG">图片</li>
                        <li class="an-w-btn" data-type="VIDEO">视频</li>
                        <li class="an-w-btn" data-type="XMIND">导图</li>
                        <li class="an-w-btn" data-type="AUDIO">音频</li>
                        <li class="an-w-btn" data-type="SOFTWARE">软件</li>
                    </ul>

                </div>
            </div>
            <div class="col-10">
                <div class="pannel-widget pad-w-2">

                    <!--搜索栏组件-->
                    <div class="search-widget">
                        <div class="s-w-inp">
                            <i class="s-w-inp-ico icon-sousuo"></i>
                            <input class="s-w-inp-text" type="text" v-model="search_data.text"
                                   placeholder="知识内容搜索 ..."/>
                        </div>
                        <span class="s-w-btn" @click="search">搜索</span>
                    </div>

                </div>
                <div class="pad-h-1"></div>
                <div class="pannel-widget pad-w-2">

                    <!--搜索结果-->
                    <div class="search-result-widget">
                        <ul v-if="search_point_result.length>0" v-show="search_point_result_show" class="sr-w-result" @click="sel_result_point">
                            <li class="sr-w-item" v-for="item of search_point_result">
                                <span class="sr-w-item-title" :id="item.point_id">{{item.title}}</span>
                                <span class="sr-w-item-desc">{{item.desc}}</span>
                            </li>
                        </ul>
                        <h2 class="sr-w-title">{{cur_point_title}}</h2>

                        <div v-if="search_result">

                            <!--搜索结果-文字类型-->
                            <div v-for="text of search_result.TEXT" class="sr-w-res-item">
                                <div class="sr-w-res-item-title">{{text.title}}<span class="sr-w-res-item-more">更多文字</span></div>
                                <div class="sr-w-res-item-p">{{text.desc}}</div>
                            </div>

                            <!--搜索结果-图片类型-->
                            <div class="sr-w-res-item" v-if="search_result.IMG">
                                <div class="sr-w-res-item-title">图片<span class="sr-w-res-item-more">更多图片</span></div>
                                <div class="grid">
                                    <div v-for="img of search_result.IMG" class="sr-w-res-item-img col-3 pad-right-1">
                                        <div class="sr-w-res-item-cover-box"><img class="sr-w-res-item-cover" src="@/assets/res/imgs/img_1.jpg" /></div>
                                        <p class="sr-w-res-item-name">{{img.title}}</p>
                                    </div>
                                </div>
                            </div>

                            <!--搜索结果-视频类型-->
                            <div class="sr-w-res-item" v-if="search_result.VIDEO">
                                <div class="sr-w-res-item-title">视频<span class="sr-w-res-item-more">更多视频</span></div>
                                <div class="grid">
                                    <div v-for="img of search_result.VIDEO" class="sr-w-res-item-img col-3">
                                        <div class="sr-w-res-item-cover-box"><img class="sr-w-res-item-cover" src="@/assets/res/imgs/img_1.jpg" /></div>
                                        <p class="sr-w-res-item-name">{{img.title}}</p>
                                    </div>
                                </div>
                            </div>

                            <!--搜索结果-导图类型-->
                            <div class="sr-w-res-item" v-if="search_result.XMIND">
                                <div class="sr-w-res-item-title">思维导图<span class="sr-w-res-item-more">更多思维导图</span></div>
                                <div class="grid">
                                    <div v-for="xmind of search_result.XMIND" class="sr-w-res-item-img col-3">
                                        <div class="sr-w-res-item-cover-box"><img class="sr-w-res-item-cover" src="@/assets/res/imgs/img_1.jpg" /></div>
                                        <p class="sr-w-res-item-name">{{xmind.title}}</p>
                                    </div>
                                </div>
                            </div>

                            <!--搜索结果-音频类型-->
                            <div class="sr-w-res-item" v-if="search_result.AUDIO">
                                <div class="sr-w-res-item-title">音频<span class="sr-w-res-item-more">更多音频</span></div>
                                <div class="grid">
                                    <div v-for="audio of search_result.AUDIO" class="sr-w-res-item-img col-3">
                                        <div class="sr-w-res-item-audio-box">
                                            <audio controls>
                                                <source src="@/assets/res/audio/audio_1.mp3"/>
                                            </audio>
                                        </div>
                                        <p class="sr-w-res-item-name">{{audio.title}}</p>
                                    </div>
                                </div>
                            </div>

                            <!--搜索结果-软件类型-->
                            <div class="sr-w-res-item" v-if="search_result.SOFTWARE">
                                <div class="sr-w-res-item-title">软件<span class="sr-w-res-item-more">更多软件</span></div>
                                <div class="grid">
                                    <div v-for="software of search_result.SOFTWARE" class="sr-w-res-item-img col-2d5">
                                        <a href="@/assets/res/software.jpg" download="download">
                                            <div class="sr-w-res-item-cover-box"><img class="sr-w-res-item-cover" src="@/assets/res/software.jpg" /></div>
                                            <p class="sr-w-res-item-name">{{software.title}}</p>
                                        </a>
                                    </div>
                                </div>
                            </div>

                        </div>

                        <p v-if="search_point_result.length==0" class="sr-w-no-result">
                            <i class="sr-w-no-result-ico icon-noresult"></i><br/>
                            <span>暂无搜索结果</span>
                        </p>
                    </div>

                </div>
                <div class="pad-h-2"></div>
            </div>
        </div>
    </div>
</template>

<script>
    import $B from "@/lib/Btx";

    var search_point_result = [//【data:知识点搜索结果】
        {
            point_id: "point_1",
            title: "Hadoop",
            desc: "Lorem ipsum dolor sit amet, consectetur adipiscing elit.",
        },
        {
            point_id: "point_2",
            title: "HBase",
            desc: "Aenean euismod bibendum laoreet. Proin gravida dolor sit amet lacus accumsan et viverra justo commodo.",
        },
        {
            point_id: "point_3",
            title: "HTML",
            desc: "Proin sodales pulvinar tempor Cum sociis natoque penatibus et magnis dis parturient montes.",
        },
    ], search_result = {//【data:内容资源搜索结果】
        TEXT: [//文字类型
            {
                text_id: "text_1",
                title: "HTML概述",
                desc: "Proin sodales pulvinar tempor Cum sociis natoque penatibus et magnis dis parturient montes."
            }
        ],
        IMG: [//图片类型
            {
                img_id: "img_1",
                title: "HTTP协议制定者",
            },
            {
                img_id: "img_2",
                title: "HTTP协议原理图1",
            },
            {
                img_id: "img_3",
                title: "HTTP协议原理图2",
            },
        ],
        VIDEO: [//视频类型
            {
                video_id: "video_1",
                title: "编写第一个JAVA程序helloworld",
            },
            {
                video_id: "video_2",
                title: "如何解析HTTP协议？",
            },
            {
                video_id: "video_3",
                title: "如何搭建JAVA运行环境？",
            },
            {
                video_id: "video_4",
                title: "一分钟了解HTTP",
            },
            {
                video_id: "video_5",
                title: "JAVA的字节码",
            },
            {
                video_id: "video_6",
                title: "JAVA虚拟机是如何编译的？",
            },
        ],
        XMIND: [//xmin类型
            {
                xmind_id: "xmind_1",
                title: "HTTP解构",
            },
        ],
        AUDIO: [//音频类型
            {
                audio_id: "audio_1",
                title: "一分钟了解HTTP",
            },
            {
                audio_id: "audio_2",
                title: "JAVA虚拟机是如何编译的？",
            },
        ],
        SOFTWARE: [//软件类型
            {
                software_id: "software_1",
                title: "Ecllipse安装包",
            },
        ]
    }

    export default {
        name: "library",
        data() {
            return {

                /*搜索内容（请求：1.知识点 -> 2.相关资源）*/
                search_data: {
                    type: ["ALL"],
                    text: ""
                },

                /*搜索结果知识点（响应）*/
                search_point_result: [],

                /*搜索结果知识点显示状态*/
                search_point_result_show: true,

                /*搜索结果标题*/
                cur_point_title: "",

                /*搜索资源结果*/
                search_result: false

            }
        },
        methods: {

            /*选择知识点资源类型*/
            sel_search_type(e) {
                $B.select("an-w-btn", e.target, () => {
                    this.search_data.type.unique_push(e.target.dataset.type);
                });
            },

            /*搜索*/
            search() {

                /*发送搜索请求数据？？？？*/
                let search_text = this.search_data.text;
                console.log(this.search_data.text);//【基于输入内容检索】
                this.reset_search();
                if(search_text == ""){//无搜索结果
                    this.search_point_result = [];
                }else{//有搜索结果
                    this.search_point_result_show = true;
                    this.search_point_result = search_point_result;
                }

            },

            /*选择知识点资源*/
            sel_result_point(e){
                let id = e.target.id;
                if(id){
                    this.search_point_result_show = false;
                    this.cur_point_title = e.target.innerHTML;

                    /*发送请求资源知识点？？？？*/
                    console.log(this.search_data.type.join(","), id);//【基于输入内容检索】
                    this.search_result = search_result;

                }
            },

            /*重置搜索*/
            reset_search(){
                this.cur_point_title = this.search_data.text = "";//清除当前标题&搜索字段
                this.search_result = false;//清空资源数据
            }

        }
    }
</script>

<style scoped>
    @import "../assets/css/pannel-widget.css";
    @import "../assets/css/seg-widget.css";
    @import "../assets/css/base-title-widget.css";
    @import "../assets/css/aside-nav-widget.css";
    @import "../assets/css/search-widget.css";
    @import "../assets/css/search-result-widget.css";
</style>


