<template>
    <div>
        <div class="pad-h-2"></div>
        <div class="container grid">
            <div class="col-2 pad-right-2">
                <div class="pannel-widget">
                    <h2 class="base-title-widget">个人中心</h2>
                    <hr class="seg-widget"/>

                    <!--侧边栏导航组件-->
                    <aside-nav-widget :init_data="aside_nav_data"></aside-nav-widget>

                </div>
            </div>
            <div class="col-10">
                <div class="pannel-widget">
                    <div class="pad-h-1"></div>
                    <div class="pad-left-2">
                        <span class="base-btn-widget">编辑简历</span>
                    </div>
                    <div class="pad-h-2"></div>
                    <h2 class="base-title-widget bt-w-blue">个人信息</h2>
                    <div class="grid">
                        <div class="col-2">
                            <img src="" />
                        </div>
                        <div class="col-8">

                            <!--基本列表文本-->
                            <div class="list-text-widget grid pad-w-2">
                                <div class="col-6">
                                    <div class="lt-w-item">
                                        <span>姓名：</span>
                                        <span class="lt-w-item-after">{{user_info.uname}}</span>
                                    </div>
                                </div>
                                <div class="col-6">
                                    <div class="lt-w-item">
                                        <span>性别：</span>
                                        <span class="lt-w-item-after"></span>
                                    </div>
                                </div>
                                <div class="col-6">
                                    <div class="lt-w-item">
                                        <span>年龄：</span>
                                        <span class="lt-w-item-after">{{user_info.age}}</span>
                                    </div>
                                </div>
                                <div class="col-6">
                                    <div class="lt-w-item">
                                        <span>电话：</span>
                                        <span class="lt-w-item-after">{{user_info.tel}}</span>
                                    </div>
                                </div>
                                <div class="col-6">
                                    <div class="lt-w-item">
                                        <span>邮箱：</span>
                                        <span class="lt-w-item-after">{{user_info.email}}</span>
                                    </div>
                                </div>
                                <div class="col-6">
                                    <div class="lt-w-item">
                                        <span>住址：</span>
                                        <span class="lt-w-item-after">{{user_info.addr}}</span>
                                    </div>
                                </div>
                                <div class="col-12">
                                    <div class="lt-w-item">
                                        <span>应聘岗位：</span>
                                        <span class="lt-w-item-after"></span>
                                    </div>
                                </div>
                            </div>

                        </div>
                        <div class="col-2 pcenter">
                            <!--<img :src="'@/assets/user/' + user_info.uid + '/erweima.png'" /><br/><span>复制链接地址</span>-->
                        </div>
                    </div>
                    <hr class="seg-widget"/>
                    <div class="pad-h-2"></div>
                    <h2 class="base-title-widget bt-w-blue">教育背景</h2>
                    <hr class="seg-widget"/>

                    <div class="pad-h-2"></div>
                    <h2 class="base-title-widget bt-w-blue">专业技能</h2>
                    <hr class="seg-widget"/>

                    <div class="pad-h-2"></div>
                    <h2 class="base-title-widget bt-w-blue">项目经验</h2>
                    <hr class="seg-widget"/>

                    <div class="pad-h-2"></div>
                    <h2 class="base-title-widget bt-w-blue">奖项荣誉</h2>
                    <hr class="seg-widget"/>

                    <div class="pad-h-2"></div>
                    <h2 class="base-title-widget bt-w-blue">自我评价</h2>
                    <hr class="seg-widget"/>
                    <div></div>
                </div>
                <div class="pad-h-2"></div>
            </div>
        </div>
    </div>
</template>

<script>
    import AsideNav from "@/components/widget/aside-nav-widget";

    var user_blog = "http://www.baidu.com",//【data:用户博客地址】
        user_info = {//【data:用户信息】
            uid: "u1",
            uname: "邢天博",
            age: 30,
            tel: 18683938348,
            email: "btxstudio@163.com",
            addr: "双流县步行街万代福大厦",
        },
        aside_nav_data = [
            {
                name: "我的成绩",
                type: "route",
                link: "/user_home",
            },
            {
                name: "我的作业",
                type: "route",
                link: "/user_home_work",
            },
            {
                name: "我的博客",
                type: "href",
                link: user_blog,
            },
            {
                name: "我的简历",
                type: "route",
                act: "act",
                link: "/user_home_resume",
            },
            {
                name: "设置",
            },
        ];

    export default {
        name: "user_home_resume",
        data() {
            return {

                /*aside_nav_widget*/
                aside_nav_data,

                /*用户信息*/
                user_info,

            }
        },
        components: {
            "aside-nav-widget": AsideNav,
        },
    }
</script>

<style scoped>
    @import "../assets/css/pannel-widget.css";
    @import "../assets/css/seg-widget.css";
    @import "../assets/css/base-title-widget.css";
    @import "../assets/css/base-btn-widget.css";
    @import "../assets/css/list-text-widget.css";
</style>


