import Vue from 'vue';
import Router from 'vue-router';

//路线图
import home from '@/components/home';
import learn_start from '@/components/learn_start';
import learn_path from '@/components/learn_path';
import learn_detail from '@/components/learn_detail';

//图书馆
import library from '@/components/library';

//竞技场
import arena from '@/components/arena';

//咖啡厅
import coffee from '@/components/coffee';

//我的（家）
import user_home from '@/components/user_home';
import user_home_work from '@/components/user_home_work';
import user_home_resume from '@/components/user_home_resume';

Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/',
            name: 'home',
            component: home
        },
        {
            path: '/learn_start',
            name: 'learn_start',
            component: learn_start
        },
        {
            path: '/learn_path',
            name: 'learn_path',
            component: learn_path
        },
        {
            path: '/learn_detail',
            name: 'learn_detail',
            component: learn_detail
        },
        {
            path: '/library',
            name: 'library',
            component: library
        },
        {
            path: '/arena',
            name: 'arena',
            component: arena
        },
        {
            path: '/coffee',
            name: 'coffee',
            component: coffee
        },
        {
            path: '/user_home',
            name: 'user_home',
            component: user_home
        },
        {
            path: '/user_home_work',
            name: 'user_home_work',
            component: user_home_work
        },
        {
            path: '/user_home_resume',
            name: 'user_home_resume',
            component: user_home_resume
        },
    ]
})



