<template>
    <div class="class-path-widget">
        <ul class="grid">
            <li class="col-3 pad-right-1" v-for="(sub_class, index) of cur_class.sub_class">
                <div class="cp-w-item">
                    <router-link :to="'/learn_detail?sub_c_id=' + sub_class.sub_c_id">
                        <div class="cp-w-item-title">第<span class="cp-w-item-title-strong">{{index+1}}</span>课</div>
                        <div class="cp-w-item-cont">
                            <h3 class="cp-w-item-sub-title">{{sub_class.name}}</h3>
                            <p>{{sub_class.desc}}</p>
                        </div>
                    </router-link>
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
    export default {
        name: "class-path-widget",
        props: ["cur_class"],
    }
</script>

<style scoped>
    @import "../../assets/css/class-path-widget.css";
</style>


