<template>
    <div class="container">
        <h2 class="main-title-widget">
            <router-link class="mt-w-link" to="/back">{{cur_sub_class.name}}</router-link>
            <span class="mt-w-text">{{class_detail.task.title}}</span>
        </h2>
        <div class="cd-w-cont pannel-widget grid">
            <div class="col-4 pad-w-2">
                <div class="video-widget">
                    <video controls>
                        <source :src="'@/assets/media/class/' + class_detail.task.video" type="video/mpeg" />
                    </video>
                </div>
            </div>
            <div class="col-8 pad-w-2">
                <h2>{{class_detail.task.title}}</h2>
                <div class="basic-article-widget">
                    <h3 class="ba-w-title">本课目标：</h3>
                    <p class="ba-w-p" v-for="class_purpose of class_purposes">{{class_purpose}}</p>
                </div>
                <div><span class="base-btn-widget" @click="send_work" :state="btn_state.state">{{btn_state.text}}</span>
                </div>
                <div class="pad-h-2"></div>
            </div>
        </div>
        <div class="pad-h-1"></div>
        <div class="cd-w-tab pannel-widget">
            <ul class="p-w-tabs" @click="sel_tab">
                <li class="p-w-tab act">任务</li>
                <li class="p-w-tab">练习</li>
            </ul>
            <div class="basic-article-widget pad-w-2">
                <p class="ba-w-p" v-for="(desc, index) of task_desc">【{{index}}】 {{desc}}</p>
            </div>
        </div>
        <div class="pright pad-h-4">
            <router-link to="/library">&gt; 去图书馆搜索“{{class_detail.task.search}}”</router-link>
        </div>
    </div>
</template>

<script>
    import $B from "@/lib/Btx";

    var cur_sub_class = {//【data:课程介绍】
            sub_c_id: "a_1",
            name: "Java初识"
        },
        class_detail = {//【data:课程详情】
            task: {
                task_id: "task_1",
                type: "TASK_OPERATE",
                video: "res_video_1.mp4",
                title: "编写Helloworld程序",
                purpose: "111Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aenean euismod bibendum laoreet. Proin gravida dolor sit amet lacus accumsan et viverra justo commodo. Proin sodales pulvinar\ntempor. Cum sociis natoque penatibus et magnis dis parturient montes, nascetur ridiculus mus. Nam fermentum, nulla luctus pharetra vulputate.",
                notic: "完成任务后，点击“提交作业”系统会给予评价，才能继续本节后面的关卡",
                search: "Java配置环境",
                desc: "开启主机后，在云主机上搭建Java运行环境。任务所需JDK版本为1.8.171，为了节省时间，已保存在云主机中，路径为：D:/任务素材/编写Helloworld程序\n编写第一个Java程序，程序保存在“D:/任务/编写Helloworld程序”文件夹中，文件命名为Helloworld.java，程序功能为：在屏幕上打印出Helloworld字符串，如图所示：",
                imgs: ["pic_1"]
            },
            practice: [
                {
                    prac_id: "prac_1",
                    type: "PRAC_TIME_LIMITED",
                    name: "完成Helloworld程序"
                },
                {
                    prac_id: "prac_2",
                    type: "PRAC_SPEED",
                    name: "规定时间内，找出全部bug（程序错误）"
                },
                {
                    prac_id: "prac_3",
                    type: "PRAC_CODING",
                    name: "按照要求，打印输出九九表"
                },
            ]
        }

    export default {
        name: "learn_detail",
        data() {
            return {

                /*课程介绍*/
                cur_sub_class: cur_sub_class,

                /*按钮状态*/
                btn_state: {
                    text: "完成任务",
                    state: "normal"
                },

                /*课程详情*/
                class_detail: class_detail

            }
        },
        computed: {

            class_purposes() {
                return this.class_detail.task.purpose.split("\n");
            },

            task_desc() {
                return this.class_detail.task.desc.split("\n");
            },

        },
        methods: {

            /*提交作业*/
            send_work() {
                this.btn_state = {
                    text: "已提交",
                    state: "visied"
                };
                //发送服务端相关数据
            },

            /*选择任务或练习*/
            sel_tab(e){
                $B.select("p-w-tab", e.target);
            }

        }
    }
</script>

<style scoped>
    @import "../assets/css/main-title-widget.css";
    @import "../assets/css/pannel-widget.css";
    @import "../assets/css/video-widget.css";
    @import "../assets/css/base-btn-widget.css";
    @import "../assets/css/basic-article-widget.css";
</style>


