<template>
    <div class="nav-widget">
        <div class="container n-w-box grid">
            <div class="n-w-left">
                <img class="n-w-logo" src="@/assets/imgs/logo.png"/>
            </div>
            <div class="n-w-center">
                <ul class="n-w-list">
                    <li class="n-w-item"><router-link class="n-w-text" to="/">路线图</router-link></li>
                    <li class="n-w-item"><router-link class="n-w-text" to="/library">图书馆</router-link></li>
                    <li class="n-w-item"><router-link class="n-w-text" to="/arena">竞技场</router-link></li>
                    <li class="n-w-item"><router-link class="n-w-text" to="/coffee">咖啡厅</router-link></li>
                </ul>
            </div>
            <div class="n-w-right col">
                <router-link to="/user_home"><span class="head-widget">{{uname}}</span></router-link>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'nav-widget',
        data(){
            return {
                uname: "天博"
            }
        }
    }
</script>

<style scoped>
    @import "../../assets/css/nav-widget.css";
    @import "../../assets/css/head-widget.css";
</style>


